.deleteModal {
  position: fixed;
  top: 60px;
  left: 0;

  height: calc(100vh - 60px);
  width: 100%;

  z-index: 150;

  background: linear-gradient(
    0deg,
    rgba(83, 61, 186, 1) 0%,
    rgba(80, 48, 154, 1) 43.14%,
    rgba(106, 70, 165, 1) 73.27%,
    rgba(133, 93, 175, 1) 120.03%
  );

  box-shadow: 0px 4px 60px 0px #00000040;
  backdrop-filter: blur(50px);

  overflow: auto;
}

.deleteModal::-webkit-scrollbar {
  display: none;
}

.modalContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  gap: 40px;

  padding: 20px;
  width: 100%;
  min-height: 100%;
}

.modalContent p {
  color: #ffffff;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  text-align: center;
}

.modalContent .buttonsWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

@media screen and (min-width: 768px) {
  .deleteModal {
    top: 0;
    min-height: 100vh;
    background: #220d5b3b;
    backdrop-filter: blur(3px);
    display: flex;
    justify-content: center;
    padding: 40px 118px;
  }

  .modalContent {
    background: rgba(82, 59, 126, 1);
    box-shadow: 0px 4px 60px 0px #00000040;
    border-radius: 8px;
    min-height: fit-content;
    height: 427px;
    width: 533px;
    padding: 60px 117px;
    margin: auto 0;
  }
}
